:root{
  --white-shady: rgb(255, 255, 255, .9);
  --white: rgb(255, 255, 255);
  --black-pitch: rgb(17, 17, 17);
  --black-light: rgb(17, 17, 17, .8);
  --black-transparent: rgb(17, 17, 17, .2);
  --black-less-transparent: rgb(17, 17, 17, .7);
  --shadow: rgb(0, 0, 0, .1);
  --green: rgb(0, 255, 110);
  --blue: rgb(0, 123, 255);
  --red: rgb(245, 66, 66)
}


/* Applies to the html document */
* {
  margin:0;
}
/* Styling app component */
.app {
  background-color: rgb(17, 17, 17);
  background-color: var(--black-pitch);
}
/* Positioning banner and color */
.banner {
    color: var(--white);
    object-fit: contain;
    height: 448px;
}

/* Positioning banner content */
.banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

/* Styling banner title */
.banner__title {
    font-size: 1.5rem;
    font-weight: 800;
    text-shadow: 2px 2px 4px var(--shadow);


}

/* Styling banner description font*/
.banner__description {
    width: 45rem;
    font-size: 1.2rem;
    max-width: 80vw;
    max-height: 300px;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);

}

/* Styling buttons */
.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-bottom: 1rem;
    background-color: rgba(51, 51, 51, 0.5);
}

/* Styling hover effect */
.banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

/* Creates a fade in banner bottom effect */
.banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), var(--black-pitch))
}

