    }


}


/* PlayPauseButton.css */
.play-pause-wrapper {
    display: flex;
    align-items: center;


    .hidden-checkbox {
        opacity: 0;
        position: absolute;
        pointer-events: none;
    }

    .button-label {
        display: grid;
        border: 2px solid var(--white);
        background-color: var(--black-pitch);
        --w: 50px;
        width: var(--w);
        height: var(--w);
        border-radius: 50%;
        place-items: center;
        cursor: pointer;
        transition-duration: .5s;
    }

    .button-label:hover {
        background-color: var(--white);
    }


    .playPauseIcon {
        --w: 50%;
        width: var(--w);
        height: var(--w);
        background: white;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
        transition: all 0.2s ease-in-out;
    }

    .button-label:hover> .playPauseIcon{
        background-color: var(--black-pitch);
    }

    .play {
        -webkit-clip-path: polygon(20% 0,
                20% 100%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%);
                clip-path: polygon(20% 0,
                20% 100%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%,
                90% 50%);
        -webkit-transform: translate(6%, 0);
                transform: translate(6%, 0);
    }

    .pause {
        -webkit-clip-path: polygon(0 0,
                0 100%,
                33.33% 100%,
                33.33% 0,
                66.66% 0,
                100% 0,
                100% 100%,
                66.66% 100%,
                66.66% 0);
                clip-path: polygon(0 0,
                0 100%,
                33.33% 100%,
                33.33% 0,
                66.66% 0,
                100% 0,
                100% 100%,
                66.66% 100%,
                66.66% 0);
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }

    .play-2 {
        -webkit-clip-path: polygon(20% 0,
                20% 100%,
                35% 90%,
                35% 90%,
                35% 90%,
                90% 50%,
                35% 10%,
                35% 10%,
                35% 10%);
                clip-path: polygon(20% 0,
                20% 100%,
                35% 90%,
                35% 90%,
                35% 90%,
                90% 50%,
                35% 10%,
                35% 10%,
                35% 10%);
        -webkit-transform: translate(6%, 0);
                transform: translate(6%, 0);
    }
}
